.intro-label {
    position: relative;
    top: -8vh;
    margin: 0 27px;
}

#slide0, #slide1, #slide2 {
    text-align: center;
    background-repeat: no-repeat;
    background-position: center;
}

#slide0 {
    background-image: url("img/subscriptions_onboarding_background.svg");
}

#slide1 {
    background-image: url("img/geolocation_onboarding_background.svg");
}

#slide2 {
    background-image: url("img/logo.svg");
}

.intro-button {
    position: relative;
    width: 50vw;
    height: 10vh;
    top: 75vh;
}

@keyframes pulse {
    from {
        transform: scale(.85);
    }
    50% {
        transform: scale(1);
    }
    to {
        transform: scale(.85);
    }
}
